@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap);
body {
    font-family: 'Patrick Hand', cursive;
}

.row-header {
    margin: 0px auto;
    padding: 0px auto;
}

.row-content {
    margin: 0px auto;
    padding: 50px 0px 50px 0px;
    border-bottom: 1px ridge;
    min-height: 400px;
}

.footer {
    background-color: var(--sub-color-2);
    margin: 0px auto;
    padding: 20px 0px 20px 0px;
    color: var(--sub-color-4);
}

.jumbotron {
    padding: 70px 30px 70px 30px;
    margin: 0px auto;
    background: var(--sub-color-2);
    color: var(--sub-color-4)
}

address {
    font-size: 80%;
    margin: 0px;
    color: var(--sub-color-4)
}

.navbar-dark {
    background-color: var(--sub-color-2);
}

.footer .btn {
    color: var(--sub-color-4);
}
.contact.content{
    height: 100%;
}
.contact form {
    padding: 49px 0;
    font-family: "Seatyio";
}

.contact-holder {
    margin: 20px auto;
    background: #f7fbfd;
    width: 407px;
    padding: 24px 0;
    border-radius: 13px;
}

.contact .form-holder {
    padding: 0 32px;
}

.contact .form-holder div:not(.contact-button) {
    background-color: var(--sub-color-4);
    color: var(--sub-color-2);  

}
.contact .form-holder div,
.contact .contact-button{
    margin: 30px 0 0;
    position: relative;
    width: 100%;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
}
.contact .form-holder div:focus-within{
    border-bottom: 2px solid var(--sub-color-2);
}
.contact label {
    position:absolute;
    color: #424242;
    transition: all .2s ease-in-out;
    font-size: 17px;
    top: 12px;
    left: 16px;
    cursor: text;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.contact input:focus,
.contact textarea:focus {
    outline: none
}

.contact input:not(:-ms-input-placeholder) +label, .contact textarea:not(:-ms-input-placeholder)+label {
    top: 1px;
    left:12px;
    font-size: 13px;
}

.contact input:focus+label,
.contact input:not(:placeholder-shown) +label,
.contact textarea:focus+label,
.contact textarea:not(:placeholder-shown)+label {
    top: 1px;
    left:12px;
    font-size: 13px;
}

.contact input,
.contact textarea {
    font-family: 'Patrick Hand', cursive;
    background-color: transparent;
    width: 100%;
    
    color: var(--sub-color-2);
    font-size: 16px;
}
.contact input{
    padding: 0 13px;
    padding-bottom: 4px;
}
.contact textarea{
    padding: 0 13px;
    padding-bottom: 4px;
}
.contact input,
.contact .text-area{
    padding-top:22px;
}

.contact .contact-button{
    background: var(--sub-color-2);
    color: #FFF;
}
.contact-button button {
    width: 100%;
    padding: 7px;
    position: relative;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.contact-button button:before{
    content: "";
    position: absolute;
    top:0;
    left:0;
    border-radius: 4px;
    width:0%;
    height:100%;
    background-color:  rgba(0, 0, 0, 0.3);
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    transition: all .3s ease-in-out;
    -webkit-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
}
.contact-button button:hover::before
{
    width:100%;
}
.contact-button button:focus,
.contact-button button:active
{
    outline: none !important;
    overflow: hidden;
}
.modal-content{
    justify-content: center;
    box-shadow: 1px 7px 14px -5px rgb(0 14 36 / 62%);
    background-color: rgba(255, 255, 255, 0.9);
    font-family: 'Patrick Hand', cursive;
}
.mr-1{
    margin-right: 0.25rem;
}
:root{
    --card-height: 482px;
}
.full-menu{
    background-image: url(/static/media/foodbcak.a62f71c1.jpg);
    z-index: -1;
    -webkit-filter: blur(8px);
            filter: blur(8px);
    background-repeat: no-repeat;
    background-size: cover;
    top: 0;
    left: 0;
}
.menu-card {
    font-family: 'Montserrat', sans-serif;
    border: 1px solid #eed8c7;
    border-radius: 3px;
    padding: 2px;
    background: #f5f5f5ad;
    height: 482px;
    height: var(--card-height);
    text-align: left;
    margin: 15px 0;
    width: 300px;
    box-shadow: 0px 0px 3px #eed8c7;
}

.menu-preview{
    height: calc(482px/2);
    height: calc(var(--card-height)/2);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right;
    text-align: start;
    box-shadow: 0px 5px 3px #685a5a38;
}
.menu-name{
    font-size: 27px;
    font-weight: 700;
}
.menu-desc{
    text-align: left;
    text-indent: 9px;
    margin: 11px 0 0;
    font-size: 13px;
}
.menu-description{
    display: flex;
    flex-direction: column;
    align-content: space-around;
    height: calc(482px/2 - 5px);
    height: calc(var(--card-height)/2 - 5px);
    justify-content: space-around;
    align-items: flex-start;
}
.menu-price{
    font-family: fantasy;
    font-size: 25px;
}
.menu-order > div{
    width:100%;
}
.menu-label, .menu-cat{
    padding: 2px 10px;
    font-size: 13px;
    border-radius: .25rem;
    color: #FFF;
    margin:0 2px;
}
.menu-label{
    border: 1px solid #f44336;
    background: #e52828;
}
.menu-cat{
    border: 1px solid #2196f3;
    background: #1565c0;
}
.menu-price::after{
    content: " $";
}
.comments{

}
.comments ul{
    padding: 1px 8px;
}
.comments ul li{
    margin: 9px 0;
    list-style: none;
    background: #fafafa8c;
    padding: 6px 10px;
    border-radius: 4px;
    box-shadow: 1px 1px 7px #00000073;
}
.comments ul li .comment-content{
    font-size: 20px;
    margin: 0 0 6px;
}
.comments ul li .comment-info{
    padding: 0 15px;
    font-size: 17px;
}
.review{
    padding: 0 9px;
    font-size: 26px;
    font-weight: 600;
}

:root {
    --main-color: #018abd;
    --sub-color-1: #004581;
    --sub-color-2: #001b48;
    --sub-color-3: #97cbdc;
    --sub-color-4: #dde8f0;
}

@font-face {
    font-family: "Seatyio";
    font-weight: 500;
    src: url(/static/media/seatyio.38d29dbe.otf) format("opentype");
}

.content {
    position: relative;
    height: 100vh;
    width: 100%;
    background: rgb(1, 138, 189);
    background: linear-gradient(0deg, rgb(1 138 189 / 69%) 10%, rgba(151, 203, 220, 1) 22%, rgba(255, 255, 255, 1) 92%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#018abd", endColorstr="#ffffff", GradientType=1);
}

.Header .content {
    margin: 5em 0;
    position: relative
}

.content h1,
.content p {
    color: #001b48;
    color: var(--sub-color-2);
}

.content h1 {
    font-family: "Seatyio";
    font-size: 60px;
}

.content p.home {
    font-size: 26px;
    width: 60%;
}
.content p, .buttons{
    font-family: 'Patrick Hand', cursive;
}

.buttons a {
    font-size: 23px;
    text-decoration: none;
    padding: 5px 19px;
    color: #eff7fa;
    background: #001b48db;
    border-radius: 100px;
    display: flex;
    align-items: center;
    height: 49px;
    width: 189px;
    justify-content: center;
}
.buttons, .def{
    margin: 30px 0;
}

.header-plate-image{
    width:100px;
    height:100px;
}
