.header {
    font-family: "Seatyio";
    margin: 10px auto;
}

.brand-nb {
    font-size: 28px;
}

.header a {
    text-decoration: none;
}

.header .menu ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
}

.menu li {
    margin: 0 9px;
    font-size: 22px
}

.menu li a {
    position: relative;
    color: #004581;
    color: var(--sub-color-1);
    transition: all .5s ease-in
}

.menu li a:active,
.menu li a.active,
.menu li a:hover {
    color: #018abd;
    color: var(--main-color);
}

.menu li a:after {
    position: absolute;
    bottom: 0;
    left: 0;
    content: "";
    width: 0px;
    height: 2px;
    background-color: #018abd;
    background-color: var(--main-color);
    transition: all .2s ease-in
}

.menu li a:hover:after {
    width: 100%;
}
