@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

:root{
    --card-height: 482px;
}
.full-menu{
    background-image: url(../images/foodbcak.jpg);
    z-index: -1;
    filter: blur(8px);
    background-repeat: no-repeat;
    background-size: cover;
    top: 0;
    left: 0;
}
.menu-card {
    font-family: 'Montserrat', sans-serif;
    border: 1px solid #eed8c7;
    border-radius: 3px;
    padding: 2px;
    background: #f5f5f5ad;
    height: var(--card-height);
    text-align: left;
    margin: 15px 0;
    width: 300px;
    box-shadow: 0px 0px 3px #eed8c7;
}

.menu-preview{
    height: calc(var(--card-height)/2);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right;
    text-align: start;
    box-shadow: 0px 5px 3px #685a5a38;
}
.menu-name{
    font-size: 27px;
    font-weight: 700;
}
.menu-desc{
    text-align: left;
    text-indent: 9px;
    margin: 11px 0 0;
    font-size: 13px;
}
.menu-description{
    display: flex;
    flex-direction: column;
    align-content: space-around;
    height: calc(var(--card-height)/2 - 5px);
    justify-content: space-around;
    align-items: flex-start;
}
.menu-price{
    font-family: fantasy;
    font-size: 25px;
}
.menu-order > div{
    width:100%;
}
.menu-label, .menu-cat{
    padding: 2px 10px;
    font-size: 13px;
    border-radius: .25rem;
    color: #FFF;
    margin:0 2px;
}
.menu-label{
    border: 1px solid #f44336;
    background: #e52828;
}
.menu-cat{
    border: 1px solid #2196f3;
    background: #1565c0;
}
.menu-price::after{
    content: " $";
}
.comments{

}
.comments ul{
    padding: 1px 8px;
}
.comments ul li{
    margin: 9px 0;
    list-style: none;
    background: #fafafa8c;
    padding: 6px 10px;
    border-radius: 4px;
    box-shadow: 1px 1px 7px #00000073;
}
.comments ul li .comment-content{
    font-size: 20px;
    margin: 0 0 6px;
}
.comments ul li .comment-info{
    padding: 0 15px;
    font-size: 17px;
}
.review{
    padding: 0 9px;
    font-size: 26px;
    font-weight: 600;
}
